.bodyContainer svg {
  width: 300px;
  height: 300px;
}

.bodyContainer svg path, .bodyContainer svg ellipse, .bodyContainer svg polygon, .bodyContainer svg rect {
  fill-opacity: 0;
  stroke: white;
  animation-name: draw;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  /* animation: 2s linear 0s 1 normal forwards running draw */
}

@keyframes draw {
  to {
    fill-opacity: 1;
    stroke-dashoffset: 0;
  }
}
